.appContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: black;
   
}

.portContainer {
    background: black;
    padding: 0 25%;
}

.portContainer * {
    transition: all 0.3s;
}

.buttonNav {
    width: 50%;
    margin-right: 45%;
    display: flex;
    justify-content: space-between;
    
}

.buttonNav {
    grid-row: 8/10;
    grid-column: 2/7;
    /* width: 50%; */
    /* margin-right: 45%; */
    /* display: flex; */
    /* justify-content: space-between; */
}

.buttonNav > a > button {
    background-color: teal;
    color: white;
    font-size: 1.5vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.buttonNav > a > button > img{
        height: 100%;
        width: 10%;
}

.apps {
    /* margin: 15vh auto 0vh; */
    /* width: 50%; */
    grid-row: 2/7;
    grid-column: 2/7;
    max-width: 950px;
    overflow: hidden;
    z-index: 6;
    border: 2px solid red;
    display: flex;
    justify-content: space-between;
}

.apps img {
    width: 100%;
    height: 100%;
    z-index: 6;
}

.appNav {
    width: 80%;
    margin: 5vh auto 10vh;
    height: 10vh;
    display: flex;
    z-index: 6;
    justify-content: space-around;
    align-items: center;
    border-radius: 8px;
}

.appNav button {
    width: 15%;
    border-radius: 12px;
    border: 3px solid transparent;
    padding: 5px;
    background: transparent;
    height: 100%;
}

.appNav button:hover {
    transform: scale(1.15);
    cursor: pointer;
}

.appNav button:focus {
    border: 1px solid red;
    border-radius: 0;
    cursor: pointer;
}

.appNav img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    position: relative;
    z-index: 6;
}

.web {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 40%;
    padding: 10px;
}

.web > img {
    width: 100%;
}

.web a {
    width: 48%;
}

.web > a > button {
    width: 50%;
    background: teal;
    color: white;
    border-radius: 4px;
    margin-top: 20px;
}

.appDisplay {
    position: relative;
}

.animateApps {
    animation: appHandle .5s;
}

@keyframes appHandle {
    0% {
        top: 0;
        opacity: 0;
    }
    2% {
        top: -25%;
        opacity: 0;
    }
    100% {
        top: 0%;
        opacity: 1;
    }
}

.infoContainer {
    grid-row: 2/9;
    grid-column: 8/10;
    border-radius: 8px;
    overflow: hidden;
    z-index: 6;
}

.info {
    display: flex;
    width: 100%;
    height: 100%;
    text-align: center;
    color: cyan;
    flex-wrap: wrap;
    z-index: 6;
    font-family: "Roboto", sans-serif;
}

.info h1 {
    font-size: 2.5vw;
    width: 100%;
    margin-top: 0;
}

.info p {
    font-size: 2vw;
    width: 100%;
    margin: 0;
}

.info a {
    font-size: 1.5vw;
    width: 50%;
    color: cyan;
}

.info > a > button {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.info > a > button > img {
    width: 20%;
    height: 90%;
}

.portfolioTitle {
    font-size: 50px;
    text-align: center;
    position: relative;
    z-index: 6;
    background-image: linear-gradient(to right, lightblue,white, lightblue);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: rainbow-animation 35s linear infinite;
}

@keyframes rainbow-animation {
    to {
        background-position: 400vh;
    }
}

.appDisplay {
    height: 60vh;
    width: 80%;
    position: relative;
    z-index: 6;
    margin: 5vh auto;
    color: white;
    display: grid;
    grid-template-rows: repeat(10, 10%);
    grid-template-columns: repeat(10, 10%);
}

.appDisplay img {
    width: 100%;
    height: 100%;
    grid-area: 1/1/6/7;
    z-index: 6;
}

.appDisplay button {
    width: 100%;
    height: 100%;
    background-color: teal;
    color: white;
    border-radius: 4px;
    z-index: 6;
}

.appDisplay button:hover {
    /* border: 3px solid red; */
    transform: scale(1.2);
    cursor: pointer;
}

.appDisplay button:focus {
    border: 2px solid lightskyblue;
    border-radius: 0;
    cursor: pointer;
}

#a1 {
    grid-area: 7/1/7/3;
}

#a1 button {
    font-size: 1rem;
}

#a2 {
    grid-area: 7/5/8/7;
}

#a2 button {
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 1rem;
}

#a2 button img {
    height: 70%;
    width: unset;
}

#appTitle {
    width: 100%;
    height: 100%;
    text-align: center;
    margin: 0;
    grid-area: 1/8/2/11;
    z-index: 6;
}

#appDesc {
    width: 100%;
    height: 100%;
    text-align: center;
    grid-area: 2/8/10/11;
    z-index: 6;
    text-shadow: 0 0 5px black;
}

#techUsed {
    display: flex;
    justify-content: space-around;
    align-items: start;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    align-content: flex-start;
    margin-top: 30px;
    z-index: 6;
}

#techUsed > img {
    height: 30px;
    margin: 4px;
    width: unset; 
    z-index: 6;
}

@media screen and (max-width:1000px) and (min-height:800px) {
    .portContainer {
        padding: 0;
    }
}

@media screen and (max-width:414px) and (min-height:600px) {

	.portContainer {
		padding: 0;
        overflow: hidden;
	}

    .appNav {
        width: 100%;
        height: 8vh;
    }

    .appNav button {
        width: 22%;
        border-radius: 12px;
        border: 3px solid transparent;
        padding: 5px;
        background: transparent;
        height: 100%;
    }

    .appDisplay img {
        grid-area: 1/1/4/11;
    }

    #appTitle {
        grid-area: 4/1/4/11;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    #appDesc {
        text-align: center;
        overflow: auto;
        grid-area: 5/1/9/11;
        position: relative;
        top: -20px;
    }

    #a1 {
        grid-area: 10/1/11/4;
    }

    #a2 {
        grid-area: 10/8/10/11;
    }


}